export default "@import url(http://weloveiconfonts.com/api/?family=zocial);\n/* zocial */\n[class*=zocial-]:before {\n  font-family: \"zocial\", sans-serif;\n}\n.container {\n  --background: #f0f0f0;\n}\n.container:hover .social li {\n  margin: 0;\n}\n.header {\n  text-align: center;\n  padding: 40px 30px 0 30px;\n}\n.header h1,\n.header h2 {\n  font-weight: 300;\n}\n.header h1 {\n  margin: 0;\n}\n.header h2 {\n  margin: 0 0 15px 0;\n  color: #505064;\n}\n.profile {\n  width: 130px;\n  height: 130px;\n  border-radius: 50%;\n  margin: 20px 0 -70px 0;\n  border: 3px solid #f37346;\n  transition: all 1.5s ease;\n}\n.profile:hover {\n  border: 3px solid red;\n}\n.content {\n  background: #f37346;\n  color: #f0f0f0;\n  padding: 75px 0 0 0;\n  text-align: center;\n  height: auto;\n}\n.content p {\n  padding: 20px 40px;\n  font-weight: 300;\n}\n.social {\n  padding: 40px 0 0 0;\n}\n.social ul {\n  list-style-type: none;\n  width: 100%;\n  height: 50px;\n  overflow: hidden;\n}\n.social ul li {\n  margin: 50px 0 0 0;\n  float: left;\n  width: 33.3333%;\n  height: 100%;\n  line-height: 50px;\n}\n.social ul li:nth-child(1) {\n  transition: 0.3s;\n}\n.social ul li:nth-child(2) {\n  transition: 0.6s;\n}\n.social ul li:nth-child(3) {\n  transition: 0.9s;\n}\n.social ul a {\n  color: #fff;\n  text-decoration: none;\n}\n.zocial-facebook {\n  background: #3B5998;\n  display: block;\n}\n.zocial-twitter {\n  background: #55ACEE;\n  display: block;\n}\n.zocial-instagram {\n  background: #3F729B;\n  display: block;\n}\n/*# sourceMappingURL=data:application/json;base64, */"